@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap');

html{
    overflow: hidden;
    width: 100%;
    height: 100%;
    text-align: center;
    font-family: "Nunito";
    margin: 0;
    padding: 0;
}

body{
    width:100%;
    height:100%;
    margin: 0;
    padding: 0;
}

#root{
    width: 100%;
    height:100%;
    margin: 0;
    padding: 0;
}

#title{
    font-family: "Lobster";
    font-size: 48px;
}

#afterRoot{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#calcdiv{
    padding: 5px;
    padding-top: 10px;
    border-radius: 4px;
    width: fit-content;
    height: fit-content;
    background: brown;
    margin-bottom: 15vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#click{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    max-height: 100%;
}


button{
    margin: 2px;
    width: 80px;
    height: 80px;
}

#display{
    background: white;
    width: 98%;
    height: 100%;
    margin-bottom: 20px;
}